import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles((theme) => ({
  authorAffiliationItem: {
    padding: theme.spacing(0, 2, 0),
    [theme.breakpoints.down('md')]: {
      padding: theme.spacing(0, 0.5, 0),
    },
  },
}));

interface Author {
  url: string;
  name: string;
  affiliation: string;
}
interface Affiliation {
  name: string;
  number: string;
}
interface Props {
  authors: Author[];
  affiliations: Affiliation[];
}

export default function Author(props: Props): React.ReactElement<Props> {
  const classes = useStyles();
  const { authors, affiliations } = props;

  return (
    <>
      <Grid container justify="center">
        {authors.map((author) => (
          <Grid
            item
            key={author.name}
            className={classes.authorAffiliationItem}
          >
            <Typography component="h3" variant="h6">
              <Link href={author.url}>{author.name}</Link>
              <sup>{author.affiliation}</sup>
            </Typography>
          </Grid>
        ))}
      </Grid>
      <Grid container justify="center">
        {affiliations.map((affiliation) => (
          <Grid
            item
            key={affiliation.name}
            className={classes.authorAffiliationItem}
          >
            <Typography component="h4" variant="h6">
              {affiliation.name}
              <sup>{affiliation.number}</sup>
            </Typography>
          </Grid>
        ))}
      </Grid>
    </>
  );
}
